.landing {
  width: 100%;
  height: 85vh;
  background-color: rgb(5, 10, 22);
  display: flex;
  background-repeat: no-repeat;
  background-position: center;
  justify-content: center;
  align-items: center;
  &--background {
    position: absolute;
    left: 10%;
    background-image: url("/assets/background.png");
    height: 90%;
    opacity: 0.14;
    width: 90%;
    background-repeat: no-repeat;
  }
  &--text-wrapper {
    width: 30%;
    height: 100%;
    display: flex;
    z-index: 1;
    justify-content: center;
    flex-direction: column;
    &--welcome {
      font-size: 28px;
      padding-bottom: 8px;
      font-family: "Bebas Neue";
      color: rgba(255, 255, 255, 0.9);
      line-height: 1.479;
      text-align: left;
    }
    &--title {
      width: 100%;
      font-size: 84px;
      padding-bottom: 12px;
      font-family: "Bebas Neue";
      color: rgb(255, 255, 255);
      line-height: 0.844;
      text-align: left;
    }
    &--title::after {
      content: " RANGEFINDER";
      color: rgb(118, 189, 74);
    }
    &--stores {
      display: flex;
      flex-direction: row;
      img {
        width: 154px;
        padding-top: 12px;
        padding-right: 12px;
      }
    }
  }
  &--phones {
    position: relative;
    width: 30%;
    height: 100%;
    z-index: 1;
    display: flex;
    &--iphone {
      position: absolute;
      top: 7%;
    }
    &--samsung {
      position: absolute;
      left: 40%;
      top: 15%;
    }
  }
}

@include respond-between(xlg, xxlg) {
  .landing {
    &--text-wrapper {
      width: 35%;
      &--welcome {
        font-size: 24px;
      }
      &--title {
        font-size: 86px;
      }
    }
    &--phones {
      width: 35%;
      img {
        height: 580px;
      }
      &--iphone {
        position: absolute;
        top: 12%;
      }
      &--samsung {
        top: 22%;
      }
    }
  }
}

@include respond-between(lg, xlg) {
  .landing {
    &--text-wrapper {
      width: 37.5%;
      &--welcome {
        font-size: 22px;
      }
      &--title {
        font-size: 74px;
      }
    }
    &--phones {
      width: 37.5%;
      img {
        height: 580px;
      }
      &--iphone {
        position: absolute;
        top: 15%;
      }
      &--samsung {
        top: 25%;
      }
    }
  }
}

@include respond-between(md, lg) {
  .landing {
    &--text-wrapper {
      width: 43.5%;
      &--welcome {
        font-size: 23px;
      }
      &--title {
        font-size: 76px;
      }
    }
    &--phones {
      width: 41.5%;
      img {
        height: 480px;
      }
      &--iphone {
        position: absolute;
        top: 20%;
      }
      &--samsung {
        top: 30%;
      }
    }
  }
}

@include respond-between(sm, md) {
  .landing {
    flex-direction: column;
    &--text-wrapper {
      width: 100%;
      align-items: center;
      height: unset;
      &--welcome {
        padding-top: 12px;
        font-size: 23px;
        text-align: center;
      }
      &--title {
        font-size: 72px;
        width: 50%;
        text-align: center;
      }
      &--stores {
        img {
          padding-top: 0;
        }
      }
    }
    &--phones {
      width: 100%;
      img {
        height: 540px;
      }
      &--iphone {
        position: absolute;
        left: 23%;
      }
      &--samsung {
        position: absolute;
        left: 49%;
      }
    }
  }
}

@include respond-between(xs, sm) {
  .landing {
    flex-direction: column;
    &--text-wrapper {
      width: 100%;
      align-items: center;
      height: 40%;
      &--welcome {
        padding-top: 12px;
        font-size: 23px;
        text-align: center;
      }
      &--title {
        font-size: 72px;
        width: 80%;
        text-align: center;
      }
      &--stores {
        img {
          padding-top: 0;
        }
      }
    }
    &--phones {
      width: 100%;
      height: 50%;
      justify-content: center;
      img {
        height: 420px;
      }
      &--iphone {
        position: unset;
        margin-left: 5%;
      }
      &--samsung {
        position: unset;
        padding-top: 5%;
      }
    }
  }
}

@include respond-between(xxs, xs) {
  .landing {
    flex-direction: column;
    &--text-wrapper {
      width: 100%;
      align-items: center;
      height: 40%;
      &--welcome {
        padding-top: 12px;
        font-size: 23px;
        text-align: center;
      }
      &--title {
        font-size: 72px;
        width: 80%;
        text-align: center;
      }
      &--stores {
        img {
          padding-top: 0;
        }
      }
    }
    &--phones {
      width: 100%;
      height: 50%;
      justify-content: center;
      img {
        height: 420px;
      }
      &--iphone {
        position: unset;
        margin-left: 5%;
      }
      &--samsung {
        position: unset;
        padding-top: 5%;
      }
    }
  }
}
